@charset "utf-8";
@import './common';

/* =====================
    map
===================== */
.map {
    position: relative;
    //height: 100vh;
    height: calc(100vh - 91px); //95px
    background: no-repeat center center / cover url(#{$image}/map/map_bg.png);
    //상단
    &__above {
        padding: 10px; //20px
        background-color: #596072;
        div {
            &:first-child {
                display: flex;
                p {
                    color: #fff;
                    font-size: 15px; //30px
                    &:nth-child(2) {
                        position: relative;
                        padding-left: 15px;
                        margin-left: 10px;
                        &::before {
                            content: '';
                            position: absolute; left: 0; top: 0;
                            width: 10px; height: 20px;
                            background: no-repeat left / 100% url(#{$image}/administration/arrow.png);
                            transform: rotate(270deg);
                        }
                    }
                }
            }
        }
        &__btn {
            img {width: 20px;}
        }
        .btn__option {
            margin-left: 10px; //20px
            padding: 6px 5px;
            border-radius: 3px;
            background: linear-gradient(150deg, #40475a, transparent);
            box-shadow: 0px 2px 3px 0px rgba(31, 31, 31, 0.32);
            text-align: center;
        }
    }

    // 하단
    &__below {
        z-index: 99;
        position: fixed; //absolute;
        bottom: 0px;
        left: 50%;
        transform: translateX(-50%);
        width: calc(100% - 10px);
        height: calc(100% - 140px); //51px
        &> div {
            position: relative;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            height: 100%;
            &> div {
                position: absolute;
                bottom: 100px;
                width: 100%;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
    &__spot {
        position: relative;
        width: calc(100% - 61px);
        &__tooltip {
            position: fixed;
            top: 50px;
            display: block;
            padding: 5px 10px;
            border-radius: 30px;
            background-color:#596072;
            span {
                position: relative;
                margin-right: 3px;//15px
                color: #fff;
                font-size: 13px;
                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 15px;
                    left: -7px;
                    width: 30px;
                    height: 21px;
                    background: no-repeat center / 50% url(#{$image}/map/map_spot_tooltip.png);
                }
            }
            img {
                width: 13px;
                margin-top: -2px;
                vertical-align: middle;
            }
        }
        .btn__spot {
            padding: 9px 20px;
            border-radius: 30px;
            background-color: #fff;
            box-shadow: 0px 2px 3px 2px rgba(75, 75, 75, 0.2);
            font-size: 14px; //24px;
            &:hover, &:active, &:focus, &:visited {
                background: linear-gradient(to right, #438de7, #4d42f3);
                color: #fff;
            }
        }
    }
    &__zoom {
        padding: 10px; //20px
        border-radius: 30px;
        background-color: #fff;
        box-shadow: 0px 2px 3px 2px rgba(75, 75, 75, 0.2);
        text-align: center;
        p {
            margin: 5px 0;
            color: #000;
            font-size: 14px //24px;
        }
    }
    &__search {
        bottom: 0 !important;
        height: 90px;
        margin: 0 auto;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
        background-color: #fff;
        form {height: 100%;}
    }
    &__slideup {
        padding: 0 15px 10px;
        p {
            margin-bottom: 3px; //15px
            font-size: 13px;
        }
        h3 {
            color: #000;
            font-size: 18px; //32px
            font-weight: 600;
            span {display: none;}
        }
        &> div {
            &:first-child {
                margin-bottom: 5px; //15px
                text-align: center;
            }
        }
        .btn-slideup {
            span {
                display: block;
                width: 40px; //80px
                height: 4px; //9px
                border-radius: 30px;
                background-color: #3c4251;
            }
        }
    }

    /* 검색 전 */
    &__form {
        display: none;
        height: 100%;
        margin-top: 10px; //20px
        &__above {
            position: relative;
            height: 100%;
            padding: 0 15px;
            .form-input {
                width: calc(100% - 21px);
                margin: 10px 0; //20px
            }
            p {
                font-size: 13px; //26px
                letter-spacing: -1px;
                span {
                    color: #608ecc;
                    font-size: 15px; //30px
                }
                &.notice {
                    color: #d43212;
                }
            }
        }
        &__btn {
            display: flex;
            justify-content: space-between;
            margin-bottom: 20px; //40px
            &> * {
                width: calc((100% - 4px) / 2);
                padding: 5px 0; //30px
                border-radius: 30px;
                font-size: 15px; //30px
                border: 1px solid #666;
                color: #666;
                &.on {
                    border: 1px solid #444548;
                    background-color: #444548;
                    color: #fff;
                    box-shadow: 0px 2px 5px 0px rgba(115, 115, 115, 0.65);
                }
            }
        }
        &__below {
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            .notice {
                margin: 0 12px 5px;
                background-color: #fff;
                font-size: 11px;
            }
        }
        &__search {
            display: block;
            width: 100%;
            padding: 14px 0; //98px
            background-color: #404040;
            color: #fff;
            font-size: 16px;
            text-align: center;
        }
    }

    //시설 유형 검색
    &__facility {
        &__con {
            overflow-y: scroll;
            height: calc(100% - 240px);
            margin-top: 10px; //20px
            li {
                &:not(:first-child) {margin-top: 15px;} //30px
                &:nth-child(2) h4::before {background-image: url(#{$image}/map/i_facility02.png);}
                &:nth-child(3) h4::before {background-image: url(#{$image}/map/i_facility03.png);}
                &:nth-child(4) h4::before {background-image: url(#{$image}/map/i_facility04.png);}
            }
            h4 {
                position: relative;
                margin-bottom: 5px; //10px
                padding-left: 25px; //50px
                font-size: 15px; //30px
                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 17px; //34px
                    height: 18px; //35px
                    background: no-repeat center / 100% url(#{$image}/map/i_facility01.png);
                }
            }
            div {
                flex-wrap: wrap;
                .option__btn {
                    width: 72px;
                    margin-right: 3px;
                    margin-bottom: 3px;
                    font-size: 14px;
                    &.fs-small {font-size: 10px;}
                }
            }
        }
    }
    
    /* 검색 후 */
    &__result {
        &__con {
            display: none;
            height: 100%;
            margin-top: 10px; //20px
            &> p {
                height: 45px; line-height: 45px;
                margin: 7px 10px;
                padding-left: 18px;
                font-size: 12px;
                letter-spacing: -1px;
                font-size: 12px; //22px
                background: no-repeat left center / 15px 22px url(#{$image}/map/i_notice--pin.png);
            }
        }
        &__tit {
            position: relative;
            padding-left: 20px; //40px
            &::before {
                content: '';
                display: block;
                position: absolute;
                bottom: 5px;
                left: 0;
                width: 14px; //28px
                height: 15px; //31px
                background: no-repeat center / 100% url(#{$image}/map/i_result--arrow.png);
            }
            h3{
                span {
                    display: inline-block;
                    color: #585f6e;
                    font-size: 16px; //28px
                    font-weight: 400;
                }
            }
        }
        &__distance {
            text-align: center;
            p {
                font-size: 13px;
                text-align: right;
                &.box {
                    margin-bottom: 0;
                    padding: 1px 8px;
                    border-radius: 3px;
                    background-color: #d6e4fa;
                    color: #000;
                    font-size: 14px;
                    text-align: center;
                }
            }
        }
        &__sort {
            padding: 0 15px;
            text-align: right;
            &> * {
                color: #585f6e;
                font-size: 13px; //26px
                font-weight: 600;
                &:last-child {
                    position: relative;
                    margin-left: 4px; //15px
                    padding-left: 7px; //15px
                    &.on {
                        padding-left: 25px;
                        &::after {
                            left: 7px;
                        }
                    }
                    &::before {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 4px;
                        left: 0;
                        width: 1px;
                        height: 10px; //20px
                        background: #b2b2b2;
                    }
                }
                &:checked, &.on {
                    position: relative;
                    padding-left: 16px;
                    color: #5d8fc9;
                    &::after {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 4px;
                        left: 0;
                        width: 13px; //25px
                        height: 9px; //18px
                        background: no-repeat center / 100% url(#{$image}/map/i_checked.png);
                    }
                }
            }
        }
        &__list {
            overflow-y: scroll;
            height: calc(100% - 155px); //365px;
            li {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 15px;
                border-bottom: 1px solid #e1e1e1;
                h4 {
                    margin-bottom: 3px; //15px
                    color: #000;
                    font-size: 18px; //32px
                    font-weight: 600;
                }
                p {
                    font-size: 13px;
                }
                img {width: 22px;} //41px
                &.on {
                    background-color: #edf4fd;
                }
            }
        }
    }
}

// 조건 검색
.option {
    z-index: 999;
    position: fixed;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    background-color: #666666;
    &__wrap {
        margin-left: 30px; //60px
        width: calc(100% - 30px);
        background-color: #ffffff;
    }
    &__header {
        padding: 15px 12px 12px;
        background-color: #404040;
        color: #fff;
        font-size: 16px; //36px
        font-weight: 500;
        img {width: 20px;}
    }
    &__body {
        overflow-y: scroll;
        height: calc(100vh - 97px);
        padding: 0 10px 10px; //20px
    }
    &__container {
        h4 {
            padding: 12px 0 7px;
            font-size: 16px; //32px
            font-weight: 500;
        }
    }
    &__content {
        flex-wrap: wrap;
        padding: 10px 10px 5px; //20px
        border-radius: 10px;
        box-shadow: 0 0 4px 2px rgba(172, 172, 172, 0.47);
        &.d-flex {padding: 10px 0 5px 10px;}
        p {
            color: #363636;
            font-size: 13px; //24px
            width: 100%;
        }
    }
    &__btn {
        min-width: 85px;
        margin-right: 5px;
        margin-bottom: 5px;
        padding: 10px 5px; //28px
        border-radius: 5px;
        border: 1px solid #e0e0e0;
        font-size: 14px; //26px;
        &:hover, &:active, &:focus, &:visited, &[aria-checked="true"] {
            border: 1px solid #448ae8;
            background-color: #e8f1fd;
        }
    }
    &__text {
        display: block;
        margin: 30px 0 10px; //62px 20px
        padding-left: 30px;
        color: #585f6e;
        font-size: 13px; //26px
        text-align: right;
        background-position: right 170px center;
    }
    .btn__wrap {
        .btn.btn__cancel, .btn.btn__submit {
            border-radius: 0px;
        }
    }
}

/* =====================
    생활권역 설정
===================== */
.type-select {
    overflow-y: scroll;
    height: calc(100% - 180px);
    &> h4 {
        font-size: 16px;
    }
    &__type {
        margin-top: 15px; //20px
        margin-bottom: 15px; //30px
        text-align: center;
        &> * {
            color: #999;
            font-size: 16px; //32px
            font-weight: bold;
            &.on {color: #000;}
            &:first-child {
                position: relative;
                margin-right: 7px; //20px
                padding-right: 10px; //20px
                &::before {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 5px;
                    right: 0;
                    width: 1px;
                    height: 12px; //24px
                    background-color: #cccccc;
                }
            }
        }
    }
    &__tab {
        margin-bottom: 5px;
        border-radius: 10px;
        border: 1px solid #d9d9d9;
        li {
            overflow: hidden;
            width: 33.333%;
            &.on {background-color: #e6effc;}
            &:not(:first-child) {
                a, span {border-left: 1px solid #d9d9d9;}
            }
            &:first-child {border-top-left-radius: 10px; border-bottom-left-radius: 10px;}
            &:last-child {border-top-right-radius: 10px; border-bottom-right-radius: 10px;}
        }
        a, span {
            display: block;
            padding: 8px 0;
            color: #000;
            font-size: 14px; //28px
            font-weight: 500;
            text-align: center;
        }
    }
    &__radio {
        display: none;
        visibility: hidden;
        &:checked + label {
            span {background-color: #e6effc;}
        }
    }
}
.area-notice {
    position: relative;
    margin-top: 13px; //27px
    margin-bottom: 28px; //56px
    padding: 15px 0 27px; //30px 55px
    border-top: 1px solid #cdcdcd;
    border-bottom: 1px solid #cdcdcd;
    h4 {
        margin-bottom: 5px; //20px
        font-size: 16px; //32px
        img {
            width: 23px; //43px
            height: 21px; //41px
        }
    }
    &__info {
        padding-left: 10px; //20px
        color: #000;
        font-size: 15px;
        li {
            position: relative;
            padding-left: 5px; //10px
            letter-spacing: -1px;
            line-height: 20px;
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 10px;
                left: 0;
                width: 2px; //3px
                height: 2px; //3px
                border-radius: 50%;
                background-color: #000;
            }
        }
    }
    &__btn {
        position: absolute;
        bottom: -14px;
        left: 50%;
        transform: translateX(-50%);
        min-width: 60px; //120px
        height: 28px;
        line-height: 28px;
        padding: 0 10px;
        border-radius: 30px;
        border: 1px solid #c4c4c4;
        background-color: #fff;
        color: #000;
        img {
            width: 15px;//30px
            transition: .3s;
            vertical-align: middle;
        }
    }
    &__tit {
        overflow: hidden;
    }
    &__con {
        overflow: hidden;
        height: 0;
        h4 {
            margin-bottom: 5px; //20px
            font-size: 16px; //32px
            img {
                width: 23px; //43px
                height: 21px; //41px
            }
        }
    }
    &__box {
        margin-top: 15px; //30px
        color: #585f6e;
        p {
            margin-bottom: 5px; //10px
            font-size: 14px; //28px
            font-weight: 500;
        }
        div {
            padding: 10px; //20px
            border-radius: 10px;
            border: 1px solid #d9d9d9;
            font-size: 13px; //26px
        }
        span {
            display: block;
            margin-top: 3px;
            margin-bottom: 17px; //35px
            font-size: 11px;
        }
    }
}
// 고정값 선택
.default-select {
    &> p {
        margin-top: 5px; //10px
        text-align: right;
    }
    &__radio {
        p {
            position: relative;
            padding-left: 24px;//28px
            color: #000;
            font-style: 15px; //30px
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                left: 0;
                width: 19px; //38px
                height: 19px; //38px
                background: no-repeat center / 100% url(#{$image}/map/i_default01.png);
            }
        }
        &:not(:first-child) {margin-top: 13px;} //27px
        &:nth-child(2) {
            p {
                &::before {
                    background-image: url(#{$image}/map/i_default02.png);
                }
            
        }}
        &:last-child {
            p {
                &::before {
                    background-image: url(#{$image}/map/i_default03.png);
                }
            }
        }
    }
    &__list {
        padding: 10px 7px; //20px 15px
        border-radius: 5px;
        border: 1px solid #d9d9d9;
    }
    &__circle {
        display: flex;
        margin-top: 3px; //7px
        padding: 7px 14px; //14px 27px
        border-radius: 10px;
        background-color: #f5f6f6;
        li {
            width: 46px; //92px
            height: 46px; //92px
            margin-right: 3px; //7px
            border-radius: 50%;
            border: 1px solid #d9d9d9;
            background-color: #fff;
            text-align: center;
            line-height: 46px;
        }
    }
}
.circle--color {
    li {
        margin-right: 7px; //13px
        border: 1px solid transparent;
    }
}

// 직접선택
.self-select {
    ul {display: flex;}
    &__form {
        margin-bottom: 5px;
        border-radius: 10px;
        border: 1px solid #d9d9d9;
        text-align: center;
        li {
            width: 25%;
            &:not(:first-child) {border-left: 1px solid #d9d9d9;}
            &:first-child > div {border-top-left-radius: 10px; border-bottom-left-radius: 10px;}
            &:last-child > div {border-top-right-radius: 10px; border-bottom-right-radius: 10px;}
            &> div {
                display: flex;
                align-items: end;
                justify-content: center;
                padding: 16px 0;
                background-color: #f5f6f6;
                span {color: #000;}
            }
        }
        p {
            height: 30px; line-height: 30px;
            border-bottom: 1px solid #d9d9d9;
            color: #000;
            span {color: #b2b2b2;}
        }
        span {font-size: 13px;}
    }
    &__circle {
        width: 46px;
        height: 46px; line-height: 46px;
        border-radius: 50%;
        border: 1px solid #d9d9d9;
        background-color: #fff;
        color: #b2b2b2;
        font-size: 14px; //28px
    }
    &__info {
        border-top: 1px solid #d9d9d9;
        border-bottom: 1px solid #d9d9d9;
        li {
            width: 33.333%;
            padding: 8px 0;
            text-align: center;
            &:not(:first-child) {border-left: 1px solid #d9d9d9;}
        }
        span {
            display: block;
            margin-bottom: 3px;
            color: #737373;
            font-size: 11px;
        }
        p {
            color: #000;
            font-size: 15px;
            font-weight: 500;
        }
    }
}


//범례
.legend {
    height: calc(100% - 90px);
    &__box {
        display: inline-block;
        width: 10px; height: 10px;
        margin-right: 3px;
        background-color: #7d3d31;
    }
    .life-industry {height: auto;}
    .life-industry__chart {padding: 0;}
    th {text-align: left;}
    &__list {
        li {
            margin-bottom: 2px;
            color: #fff;
            &::after{display: block;content: '';clear: both;}
            &:first-child {div {background-color: #7d3d31;}}
            &:nth-child(2) {div {background-color: #9d5848;}}
            &:nth-child(3) {div {background-color: #c9795e;}}
            &:nth-child(4) {div {background-color: #d29171;}}
            &:nth-child(5) {div {background-color: #ddc178;}}
            &:nth-child(6) {div {background-color: #e5db9e;}}
            &:nth-child(7) {div {background-color: #f2eee3; border: 1px solid #cbcbcb;}}
            &:last-child {div {background-color: #fff; border: 1px solid #cbcbcb;}}
            + li + li + li + li + li {color: #404040;}
        }
        div {
            float: left;
            padding: 3px 10px;
            &:first-child {
                width: 70%;
                margin-right: 2px;
                text-align: right;
            }
            &:last-child {
                width: calc(100% - (70% + 2px));
            }
        }
    }
}

/* =====================
    
===================== */
//사업체 주요 생활 업종(선택 전)
.chart-wrap {
    margin: 5px 0 20px;
    box-sizing: border-box;
}
.chart {
    &__con {min-height: 150px;}
    &__legend {
        li {
            display: flex;
            align-items: baseline;
            span {
                display: block;
                width: 10px; height: 10px;
                margin-right: 5px;
                border-radius: 50%;
            }
            p {width: calc(100% - 15px);}
        }
    }
    &--left {&.chart__con {width: 55%;}}
    &--right {&.chart__legend {width: 45%;}}
    &--up {&.chart__con {width: 100%; height: 80%;}}
    &--down {
        &.chart__legend {
            width: 100%; height: 20%;
            padding: 0 10%;
            padding-bottom: 10px;
        }
    }
}
.life-industry {
    display: none;
    height: 100%;
    .type-select__type {margin-top: 20px;}
    .area-notice__btn {
        width: auto;
    }
    dl {
        display: flex;
        align-items: center;
        padding: 10px 15px; //20px 30px
        background-color: #eaeaea;
        box-shadow: inset rgba(133, 133, 133, 0.35) 0px 1px 4px 0px;
        dt, button {
            color: #585f6e;
            font-size: 15px; //30px
            font-weight: 500;
        }
        dt {
            width: 60px;
            margin-right: 5px;
        }
        dd {width: calc(100% - 60px);}
        button {
            width: 55px;
            padding: 4px 0;
            border-radius: 30px;
            border: 1px solid #666666;
            background-color: #fff;
            &.on {
                border-color: #444548;
                background-color: #444548;
                color: #fff;
                box-shadow: 0px 2px 4px 1px rgba(115, 115, 115, 0.65);
            }
        }
    }
    &__con {
        overflow-y: scroll;
        height: calc(100% - 170px);
    }
    &__chart {
        margin-top: 20px;
        margin-bottom: 10px;
        padding: 0 10px;
