.furniture {
    &__box {
        margin-top: 3px;
        &::after {display: block;content: '';clear: both;}
        &> div {
            margin-top: 2px;
            &> div {
                float: left;
                text-align: center;
                &:first-child {
                    width: calc(70% - 2px);
