//생활권역 통계지도 이용 안내
.map-info {
    display: none;
    z-index: 999;
    overflow-y: scroll;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #fff;
    &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 12px 15px;
        background-color: #404040;
        color: #fff;
        h3 {
            font-size: 18px;
            font-weight: 500;
        }
        img {width: 15px;}
    }
    &__body {
        padding: 15px;
    }
    &__list {
        margin-bottom: 20px;
        li {
            position: relative;
            padding: 7px 0;
            padding-left: 20px;
            border-bottom: 1px solid #cdcdcd;
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 16px;
                left: 10px;
                width: 3px;
                height: 3px;
                border-radius: 50%;
                background-color: #000;
            }
            a {
                display: block;
                color: #000;
                font-size: 15px;
            }
        }
    }
    &__con {
        h4 {
            margin-bottom: 5px;
            color: #000;
            font-size: 18px;
        }
        p {
            margin: 5px 0;
            color: #000;
            font-size: 13px;
        }
        span {
            display: block;
            margin-top: 5px;
            color: #585f6e;
            font-size: 12px;
        }
        &:not(:first-of-type) {
            margin-top: 25px;
            padding-top: 25px;
            border-top: 1px solid #cdcdcd;
        }
        ol {
            li {
                position: relative;
                padding-left: 10px;
                &::before {
                    content: '1.';
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                }
                &:nth-child(2)::before {
                    content: '2.';
                }
            }
        }
        table {
            width: 100%;
            text-align: center;
            th, td {
                padding: 7px 0;
                color: #000;
                font-size: 13px;
                border-bottom: 1px solid #cdcdcd;
                &:first-child {border-right: 1px solid #cdcdcd;}
            }
            th {
                border-top: 1px solid #cdcdcd;
                background-color: #f9f9f9;
            }
        }
    }
    &__box {
        li {
            margin-top: 10px;
            padding: 14px 10px;
            border-radius: 5px;
            border: 1px solid #c4c4c4;
            text-align: center;
            h5 {
                margin-bottom: 10px;
                padding-bottom: 10px;
                border-bottom: 1px solid #e1e1e1;
                color: #000;
                font-size: 16px;
            }
        }
    }
}

