//지도에서 지점 선택
.point{
    &__tit {
        margin-bottom: 3px;
        font-size: 15px; //30px
        font-weight: 500;
    }
    &__radio {
        margin-bottom: 6px; //12px
    }
    &__con {
        padding: 10px; //20px
        border-radius: 3px;
        border: 1px solid #dbdbdb;
        background-color: #fafafa;
        p {
            padding-left: 5px; //10px
            font-size: 14px; //28px
            font-weight: 500;
        }
    }
    &__address {
        margin-top: 8px;
        margin-bottom: 2px; //4px
        dl {
            display: flex;
            align-items: baseline;
            padding-left: 25px;
            font-size: 12px;
            color: #585f6e;
            &:first-of-type {
                margin-bottom: 1px;
                dt {background-color: #3d4353; color: #fff;}
            }
            &:last-of-type {
                dt {background-color: #e1e1e1; color: #585f6e;}
            }
        }
        dt {
            width: 40px; //75px 30px
            margin-right: 5px; //10px
            text-align: center;
        }
        dd {letter-spacing: -1px;}
    }
    &__list {
        overflow-y: scroll;
        height: 70px;
        padding-left: 25px;
        font-size: 12px;
        li {
            border-top: 1px dashed #d5d5d5;
        }
        a {
            display: block;
            padding: 3px;
            color: #585f6e;
            &:hover, &:active, &:focus, &:visited {
                background-color: #dde8fa;
            }
        }
    }
}

.point__select {
    top: 30%;
    display: none;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -8px;
        left: 50%;
        transform: translateX(-50%);
        width: 16px;
        height: 8px;
        background: no-repeat center / contain url(#{$image}/map/i_map--arrow.png);
    }
}

//지도에서 지점 확인
.point__confirm {
    display: none;
    top: 30%;
    width: 75%; //225px;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -8px;
        left: 70%;
        transform: translateX(-50%);
        width: 16px;
        height: 8px;
        background: no-repeat center / contain url(#{$image}/map/i_map--arrow.png);
    }
    .modal__body {padding: 7px;}//15px
    p {margin-bottom: 10px;} //20px
    .point__address {
        dl {
            padding-left: 0;
        }
    }
}

//고정값 선택 레이어 팝업 
.default-layer {
    display: none;
    top: 40%;
    z-index: 99;
    width: 250px;
    p {
        padding: 25px 0;
        color: #000;
        font-size: 14px;
        text-align: center;
    }
}

//주행시간
.distance-layer {
    top: 40%;
    width: 230px;
    .modal__header img {width: 12px; vertical-align: middle;}
    .modal__body {
        padding: 0;
        text-align: center;
    }
    p {
        margin-top: 10px;
        color: #000;
        font-size: 14px;
        b {
            color: #d43212;
            font-size: 16px;
        }
    }
    span {
        display: block;
        margin-bottom: 10px;
        color: #585f6e;
        font-size: 9px;
    }
    .color__list {
        display: flex;
        padding: 7px;
        background-color: #f1f1f1;
        border-bottom-left-radius: 8px;
        border-bottom-right-radius: 8px;
        dt {
            width: 45px;
            margin-right: 5px;
            color: #585f6e;
            font-size: 9px;
        }
        dd {width: calc(100% - 51px);}
        ul {display: flex;}
        li {
            width: 15px;
            height: 15px;
            margin-right: 3px;
            &:hover, &:active, &:focus, &:visited, &.on {
                border: 3px solid #008aff;
            }
        }
    }
}

//간격 
.distance_select-layer {
    top: 50%;
    transform: translate(-50%, -50%);
    .modal__body {
        padding: 20px;
        &::after {display: block;content: '';clear: both;}
    }
    .notice {margin-bottom: 3px;}
    span {
        float: right;
        font-size: 11px;
    }
}
.distance_select__con {
    display: flex;
    flex-flow: row wrap;
    margin: 5px 0;
    border: 1px solid #c4c4c4;
    border-right: none;
    border-bottom: none;
    &> * {
        width: 20%;
        padding: 5px 0;
        border-bottom: 1px solid #c4c4c4;
        border-right: 1px solid #c4c4c4;
        &:hover, &:active, &:focus, &:visited, &[aria-checked="true"] {
            background-color: #eaf1fc;
        }
    }
    &.col-4 {&> * {width: 25%;}}
    &.col-2 {&> * {width: 50%;}}
    &.col-1 {&> * {width: 100%;}}
    & + div {float: left;}          
}



@media screen and (max-width:360px) {
    .default-select__circle li {
        width: 38px;
        height: 38px;
        line-height: 38px;
    }
    .distance_select__con > * {font-size: 11px;}
    .life-industry {
        dl {
            button {width: 50px;}
        }
        .point__radio {
            p {
                font-size: 11px;
                letter-spacing: -1px;
            }
        }
        &__chart {
            p {font-size: 12px;}
        }
        &__table {
            table {font-size: 12.5px;}
            td {padding: 8px 5px;}
        }
        &__detail {
            p {font-size: 13px;}
        }
        &__tab {
            &__con {
                .distance_select__con > * {padding: 6px 0;}
            }
        }
    }
    .point__address {
        dl {padding-left: 20px;}
    }
}